.register-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #e65100 0%, #ff6b35 50%, #ff8746 100%);
  position: relative;
  overflow: hidden;
  transition: background 0.5s ease;

  // 다크 모드 배경
  &.dark-mode {
    background: linear-gradient(135deg, #1a1a2e 0%, #16213e 50%, #0f3460 100%);

    .register-card {
      background: #1e1e2e;
      border: 1px solid rgba(255, 255, 255, 0.1);
      box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5), 
                  0 8px 32px rgba(0, 0, 0, 0.3);

      .register-header {
        .subtitle {
          color: #b0b0c0 !important;
        }
      }

      .register-form {
        background: #1e1e2e;
        color: #e0e0e0;

        .form-field {
          .full-width {
            ::ng-deep .mat-mdc-text-field-wrapper {
              background-color: #2a2a3a !important;
            }

            ::ng-deep .mat-mdc-form-field-label {
              color: #b0b0c0 !important;
            }

            ::ng-deep .mat-mdc-form-field-input {
              color: #ffffff !important;
            }

            ::ng-deep .mdc-text-field--filled:not(.mdc-text-field--disabled) {
              background-color: #2a2a3a !important;
            }

            ::ng-deep .mat-mdc-form-field-focus-overlay {
              background-color: transparent !important;
            }

            ::ng-deep .mat-mdc-icon-button {
              color: #b0b0c0 !important;
            }

            ::ng-deep .mdc-line-ripple::after {
              border-bottom-color: #90caf9 !important;
            }

            ::ng-deep .mat-mdc-form-field-error {
              color: #ff6b6b !important;
            }

            ::ng-deep .mat-mdc-form-field-hint {
              color: #808090 !important;
            }
          }
        }

        .error-message {
          background: rgba(255, 107, 107, 0.15) !important;
          color: #ff6b6b !important;
          border: 1px solid rgba(255, 107, 107, 0.3);

          mat-icon {
            color: #ff6b6b !important;
          }
        }

        .login-link {
          span {
            color: #b0b0c0;
          }

          button {
            color: #90caf9 !important;

            &:hover {
              background: rgba(144, 202, 249, 0.1) !important;
            }
          }
        }
      }
    }
  }

  // 테마 토글 버튼
  .theme-toggle {
    position: absolute;
    top: 20px;
    right: 20px;
    z-index: 10;
    background: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.3);
    color: white;
    width: 48px;
    height: 48px;
    transition: all 0.3s ease;

    &:hover {
      background: rgba(255, 255, 255, 0.3);
      transform: scale(1.1) rotate(15deg);
    }

    mat-icon {
      font-size: 24px;
      width: 24px;
      height: 24px;
    }
  }

  .register-card {
    width: 100%;
    max-width: 500px;
    margin: 20px;
    background: white;
    border-radius: 16px;
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.2), 
                0 8px 32px rgba(229, 81, 0, 0.4);
    overflow: hidden;
    z-index: 2;
    position: relative;
    border: 1px solid rgba(255, 107, 53, 0.2);

    .register-header {
      background: linear-gradient(135deg, #e65100, #d84315);
      color: white;
      padding: 32px 24px;
      text-align: center;
      position: relative;

      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: linear-gradient(45deg, transparent 30%, rgba(255, 255, 255, 0.1) 50%, transparent 70%);
        animation: shimmer 3s infinite;
      }

      .mode-indicator {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 8px;
        z-index: 1;
        position: relative;

        mat-icon {
          font-size: 40px;
          width: 40px;
          height: 40px;
          filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.3));
        }

        h2 {
          margin: 0;
          font-size: 24px;
          font-weight: 600;
          text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
        }
      }

      .subtitle {
        margin: 8px 0 0 0;
        font-size: 14px;
        color: rgba(255, 255, 255, 0.9);
        z-index: 1;
        position: relative;
        text-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
      }
    }

    .register-form {
      padding: 32px 24px;
      background: white;

      .form-field {
        margin-bottom: 16px;

        .full-width {
          width: 100%;

          ::ng-deep .mat-mdc-form-field-subscript-wrapper {
            margin-top: 4px;
          }

          ::ng-deep .mat-mdc-form-field-hint {
            font-size: 12px;
            color: #666;
          }
        }
      }

      .error-message {
        display: flex;
        align-items: center;
        gap: 8px;
        background: #ffebee;
        color: #c62828;
        padding: 12px 16px;
        border-radius: 8px;
        margin-bottom: 20px;
        font-size: 14px;

        mat-icon {
          font-size: 18px;
          width: 18px;
          height: 18px;
        }
      }

      .form-actions {
        margin: 28px 0 24px;

        .register-btn {
          height: 52px;
          font-size: 16px;
          font-weight: 600;
          border-radius: 12px;
          display: flex;
          align-items: center;
          justify-content: center;
          gap: 8px;
          background: linear-gradient(135deg, #e65100, #d84315);
          border: none;
          box-shadow: 0 4px 12px rgba(229, 81, 0, 0.3);
          transition: all 0.3s ease;

          &:hover:not(:disabled) {
            background: linear-gradient(135deg, #d84315, #bf360c);
            box-shadow: 0 6px 20px rgba(229, 81, 0, 0.4);
            transform: translateY(-2px);
          }

          &:disabled {
            opacity: 0.6;
            cursor: not-allowed;
          }

          mat-spinner {
            margin-right: 8px;

            ::ng-deep circle {
              stroke: white;
            }
          }
        }
      }

      .login-link {
        text-align: center;
        padding: 20px 0 8px;
        border-top: 1px solid #e0e0e0;

        span {
          font-size: 14px;
          color: #666;
          margin-right: 4px;
        }

        button {
          font-size: 14px;
          font-weight: 600;
          color: #e65100;
          transition: all 0.2s ease;

          &:hover {
            background: rgba(229, 81, 0, 0.08);
          }
        }
      }
    }
  }
}

// 애니메이션
@keyframes shimmer {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}

// 반응형 디자인
@media (max-width: 600px) {
  .register-container {
    .register-card {
      margin: 0;
      border-radius: 0;
      min-height: 100vh;
      max-width: 100%;

      .register-form {
        padding: 24px 20px;

        .form-field {
          margin-bottom: 14px;
        }
      }
    }

    .theme-toggle {
      top: 16px;
      right: 16px;
      width: 44px;
      height: 44px;
    }
  }
}
